
h1
{
  font-size: 20px;
  margin-left: 20px;
  margin-top: 20px;
  font-family: 'Days One', sans-serif ;
  font-weight: lighter;
  text-shadow: 2px 4px 2px rgba(0,0,0,0.6);
  color:rgb(216, 211, 211);
  
}
h1 i{
  font-size: 20px;
  margin-left: 5px;
  color:rgb(216, 211, 211);
 
}

/*    FORM     */
.div-container
{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 6.25rem;
  width: auto;
  height: 6.25rem;
  justify-content: center;
}
.form
{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin: 0px auto;
 
}
.contain-input
{
  border: 1px solid #E7E7EB;
  height: 38px;
  border-radius: 5px;
  background-color: rgb(39, 39, 39);
  opacity: 0.6;
}


#nameCity
{
  background-color: transparent;
  outline: none;
  border: none;
  line-height: 38px;
  color: snow;
  font-size: 15px;
}
#nameCity::placeholder
{ color: white;
  font-size: 14px;
}

#icono
{
  color:  #78797a;
  margin-left: 5px;
}

#button-search
{
  width: 70px;
  height: 38px;
  padding: 6px;
  font-size: 14px;
  margin-left: 10px;
  background-color: #50b2e4;
  border: green;
  border-radius: 5px;
  color:snow;
  cursor:pointer;
}

#button-search:hover
{
  background: rgb(45, 188, 245);
}

/*  componente infoweather          */


#today
{
  display: none;
  font-family: "roboto";
  text-align: center;
  color: rgb(0, 0, 0);
  width: 300px;
  height: 300px;
  background-image: linear-gradient(10deg, #a5d9f4 0, #6cb4d8 50%, #1d90bd 100%);
  justify-content: center;
  border: 1px solid #E7E7EB;
  border-radius: 5px;
  opacity:0.9;
  
}

@keyframes fadein{
  0%{opacity: 0 ;}
  100%{opacity:0.9;}
}

.temp
{
 font-size: 30px;
 font-family: Arial, Helvetica, sans-serif;
 font-weight: bold;
}
.description
{
 margin-top: 10px;
  font-size: 20px;
}
.city
{
  font-size: 20px;
  font-weight: bold;
  margin-top: 20px;
}
.details
{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 5px;
  font-family:"roboto";
  font-size: 14px;
  border-top: 1px solid rgb(54, 53, 53);
  margin: 10px;
  padding-top: 10px;
}

.humidity{
  margin-left: 8px;
}

.windspeed
{
  margin-left: 7px;
}

#location-icon
{
  margin-right: 10px;
}

/*      FOOTER      */
footer
{
    width: 100%;
    position: fixed;
    clear: both;
    bottom: 0;
    left: 0 ;
    height: 60px;
    line-height: 60px;
    background: rgb(53, 52, 52);
    font-size: 20px;
    color:rgb(216, 211, 211);
    text-align: center;
    font-family: 'Days One', sans-serif;
}
/*      SOCIAL MEDIA ICONS     */

footer a {
  text-decoration: none;
  color: RGB(255,255,255)
}
#face{
  width: 25px;
  height: 25px;
  line-height: 25px;
  color:white;
  border-radius: 5px;
  background-color:RGB(72,103,170) ;
  margin-right: 10px;
  margin-left: 20px;
  font-size: 20px;
}
#tw{
  background-color: RGB(29,161,242);
  margin-right: 10px;
  line-height: 25px;
  font-size: 20px;
  border-radius: 5px ;
  width: 25px;
  height: 25px;
}
#insta{
  width: 25px;
  height: 25px;
  line-height: 25px;
  border-radius: 5px;
  background-image: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
  box-shadow: 0px 3px 10px rgba(0,0,0,.25);
  font-size: 20px;
}


/*             */
@media (max-width:768px){
  .div-container{
    margin-top: 30px;
  }
}


@media (max-width:425px){
  .div-container{
    margin-top: 15px;
  }
  footer{
    font-size: 15px;
  }
}

@media (max-width:320px){
  .div-container{
    height: 60px;
  }
  form{
    width: 269px;

    
  }
  #today{
    width: 267px;
    margin-top: 0px;
  }
  .details{
    font-size: 12px;
  }

  #nameCity{
    width: 160px;
  }
  footer{
    font-size: 13px;
  }

  #insta,
  #face,
  #tw{
    font-size: 15px;
  }
}
@media (max-width:375px){

  .form{
   margin-left: 24px;
   margin-right: 24px;
   
  }
}
